<template>
	<Content :class="{'icon-loading': loading}" app-name="calcexample">
		<AppNavigation>
			<ul id="app-calcexample-navigation">
				<AppNavigationItem title="Machine 1" icon="icon-file" />
			</ul>
		</AppNavigation>
		<AppContent>
			<div class="section">
				<h2>Calculation 1</h2>
				<p>This is an example for an secure serverside calculation</p>
				<br>
				<input id="input1"
					v-model="inputVal1"
					type="number"
					name="input1"
					placeholder="Input 1">
				<input id="input2"
					v-model="inputVal2"
					type="number"
					name="input2"
					placeholder="Input 2">
				<button class="primary"
					@click="calc({
						val1: inputVal1,
						val2: inputVal2,})">
					Calculate
				</button>
				<br>
				<br>
				<div id="result_container">
					<span>
						Result:
					</span>
					<input id="result"
						v-model="result"
						type="number"
						readonly="readonly"
						placeholder="Result will be displayed">
				</div>
			</div>
		</AppContent>
	</Content>
</template>

<script>
import Content from '@nextcloud/vue/dist/Components/Content'
import AppContent from '@nextcloud/vue/dist/Components/AppContent'
import AppNavigation from '@nextcloud/vue/dist/Components/AppNavigation'
import AppNavigationItem from '@nextcloud/vue/dist/Components/AppNavigationItem'

import axios from '@nextcloud/axios'

export default {
	name: 'App',
	components: {
		Content,
		AppContent,
		AppNavigation,
		AppNavigationItem,
	},
	data: function() {
		return {
			loading: false,
			result: null,
			inputVal1: null,
			inputVal2: null,
			show: true,
			starred: false,
		}
	},
	methods: {
		addOption(val) {
			this.options.push(val)
			this.select.push(val)
		},
		previous(data) {
			console.debug(data)
		},
		next(data) {
			console.debug(data)
		},
		close(data) {
			console.debug(data)
		},
		newButtonAction(e) {
			console.debug(e)
		},
		log(e) {
			console.debug(e)
		},
		/**
		 * Calculates Result
		 * @param {int} res Calculation Result
		 */
		async calc(res) {
			this.updating = true
			try {
				const response = await axios.get(OC.generateUrl('/apps/calcexample/calc'), {
					params: {
						val1: res.val1,
						val2: res.val2,
					},
				})
				this.result = response.data
			} catch (e) {
				console.error(e)
				OCP.Toast.error('Could not calculate')
			}
			this.updating = false
		},
	},
}
</script>
